import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http'

@Injectable({
  providedIn: 'root'
})
export class OktaService {

  constructor(private http:HttpClient) { }
  
  getUsersData()
  {
    return this.http.get('src/assets/OktaUser_search_data.json');
  }

  
}
