import { Component, Renderer } from '@angular/core';
import { OktaService } from '../okta.service';
import {AppService} from '../app.service';
import  {filter,find}  from 'rxjs/operators';
import {pipe} from 'rxjs';



interface WebUserObj{
  ignoreAdmins:any;
}

@Component({
  selector: 'app-okta',
  templateUrl: './UserSearch.component.html',
  styleUrls: ['./UserSearch.component.css']
  
})
export class UserSearchComponent  {
  
  clickMessage: string;
  ShowGroup: boolean=true;
  ShowApps: boolean;
  txtSearch:string;
  btnGroup:string;
  UserProfile: Object;
  UserData: Object;
  MainDiv:boolean;
  NoData:boolean=true;
  NoUserFound:boolean;
  obj:WebUserObj;
  result:any;
  Alert1:boolean;
  Alert2:boolean;
  selectedItem: any;

  
  constructor(private data:OktaService,private ProfileData:AppService,private renderer: Renderer) { 
   
  }
  
  ngOnInit() {
    
    this.UserProfile=JSON.parse(localStorage.getItem('userProfile'));
    this.obj=JSON.parse(localStorage.getItem('userProfile'));
    console.log(this.obj.ignoreAdmins)
  }
  
  
  onGroupClick(event, newValue) { 
    this.selectedItem = newValue;
    this.ShowGroup=true;
    this.ShowApps=false;
  }
  onAppClick(event, newValue) {
    this.selectedItem = newValue;
    
    this.ShowApps=true;
    this.ShowGroup=false;
  }
  onbtnSearch()
  {
   
    if(this.txtSearch!=undefined && this.txtSearch.trim()!="" )
    {
      var searchelm=this.txtSearch;
      var EMAIL_REGEXP = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
      if (this.txtSearch != "" && (this.txtSearch.length <= 5 || !EMAIL_REGEXP.test(this.txtSearch))) {
          this.Alert1=false;
          this.Alert2=true;
          return false;
      }
       this.UserData=this.data.getUsersData().subscribe(data=>this.UserData=data);
       this.result=(this.obj.ignoreAdmins).filter(function(element){
         return element==searchelm;
       })  
       console.log(this.result);
       if(this.result!=null && this.result!=''){
        this.Alert1=false;
        this.Alert2=false;
         this.MainDiv=true;
         this.NoData=false;
         this.NoUserFound=false;
      }
      else{
        this.Alert1=false;
        this.Alert2=false;
        this.MainDiv=false;
         this.NoData=false;
         this.NoUserFound=true;
      }
      
    }
    else{
      
      this.Alert1=true;
      this.Alert2=false;
    }
  }
  onclosealert1()
  {
    this.Alert1=false;
  }
  onclosealert2()
  {
    this.Alert2=false;
  }
}